<script>
import $ from 'jquery';
import * as echarts from 'echarts';
//import axios from 'axios';
//import 'echarts4/map/js/province/hunan.js';
import 'echarts/theme/vintage.js';
import 'echarts-wordcloud';
export default {
	mounted(){
	    this.drawPieChart(); 
		this.drawBarChart();
		this.drawTreeChart();
		this.drawScatterChart();
		this.drawMapchart();
		this.drawWordCloud1();
		this.drawWordCloud2();
		this.drawWordCloud3();
		this.drawMulitiRadar()
	    },
	methods: {
		drawPieChart() {
		  // domecharts  main
		  var myChart = echarts.init(document.getElementById("pie-nest"));
		  // 
		  var option = {
		    
		      tooltip: {
		        trigger: 'item',
		        formatter: '{a} <br/>{b}: {c} ({d}%)'
		      },
			  legend: {
			      data: [
			        '',
			        '',
			        '',
			        '',
			        '',
			        '',
			        '',
			        '',
			        '',
			        '',
					'',
					''
			      ]
			    },
		      series: [
		        {
		          name: '',
		          type: 'pie',
		          selectedMode: 'single',
		          radius: [0, '30%'],
		          label: {
					formatter: ' {d}%\n{b}',
		            position: 'inner',
		            fontSize: 14,
					color: '#fff',
		          },
		          labelLine: {
		            show: false
		          },
		          data: [
		            { value: 3124, name: ''},
		            { value: 6312, name: '' },
		            { value: 25592, name: ''}
		          ]
		        },
		        {
		          name: '',
		          type: 'pie',
		          radius: ['45%', '60%'],
		          labelLine: {
		            length: 30
		          },
		          label: {
		            formatter: ' {b|{b}}{c}  {per|{d}%}  ',
		            backgroundColor: '#F6F8FC',
		            borderColor: '#8C8D8E',
		            borderWidth: 1,
		            borderRadius: 4,
		            rich: {
		              a: {
		                color: '#6E7079',
		                lineHeight: 22,
		                align: 'center'
		              },
		              hr: {
		                borderColor: '#8C8D8E',
		                width: '100%',
		                borderWidth: 1,
		                height: 0
		              },
		              b: {
		                color: '#4C5058',
		                fontSize: 14,
		                fontWeight: 'bold',
		                lineHeight: 33
		              },
		              per: {
		                color: '#fff',
		                backgroundColor: '#4C5058',
		                padding: [3, 4],
		                borderRadius: 4
		              }
		            }
		          },
		          data: [
		            { value: 52, name: '' },
		            { value: 136, name: '' },
		            { value: 301, name: '' },
		            { value: 210, name: '' },
		            { value: 469, name: '' },
		            { value: 462, name: '' },
		            { value: 492, name: '' },
		            { value: 423, name: '' },
					{ value: 372, name: '' }
		          ]
		        }
		      ]
		  };
		  // 
		  myChart.setOption(option);
		  window.addEventListener("resize", function() {
		              myChart.resize()
		            })
		},
		drawBarChart() {
			
			// domecharts  main
			var myChart = echarts.init(document.getElementById("dynamic-bar"));
			var pending = false;
			
			const updateFrequency = 2000;
			const dimension = 0;
			const issueColors = {
			  '': '#00008b',
			  '': '#f00',
			  '': '#ffde00',
			  '': '#005500',
			  '': '#003580',
			  '': '#00ff00',
			  '': '#000',
			  '': '#003897',
			  '': '#f93',
			  '': '#bc002d',
			  '': '#aaffff',
			  '': '#000'
			};
			$.when(
			  $.getJSON('data/top10-year.json')
			).done(function (res0) {
			  const data = res0;
			  //console.log(data);
			  const years = [];
			  for (let i = 0; i < data.length; ++i) {
			    if (years.length === 0 || years[years.length - 1] !== data[i][2]) {
			      years.push(data[i][2]);
			    }
			  }
			  //console.log(years);
			  let startIndex = 1;
			  let startYear = years[startIndex];
			  // 
			  var option = {
			    grid: {
			      top: 10,
			      bottom: 30,
			      left: 150,
			      right: 80
			    },
			    xAxis: {
			      max: 50,
			      axisLabel: {
			        formatter: function (n) {
			          return n + '%';
			        }
			      }
			    },
			    dataset: {
			      source: data.slice(1).filter(function (d) {
			        return d[2] === startYear;
			      })
			    },
			    yAxis: {
			      type: 'category',
			      inverse: true,
			      max: 10,
			      axisLabel: {
			        show: true,
			        fontSize: 14,
			        formatter: function (value) {
					  //console.log(value);
			          return value;
			        }
			      },
			      animationDuration: 300,
			      animationDurationUpdate: 300
			    },
			    series: [
			      {
			        realtimeSort: true,
			        seriesLayoutBy: 'column',
			        type: 'bar',
			        itemStyle: {
			          color: function (param) {
			            return issueColors[param.value[1]] || '#5470c6';
			          }
			        },
			        encode: {
			          x: dimension,
			          y: 3
			        },
			        label: {
			          show: true,
			          precision: 1,
			          position: 'right',
			          valueAnimation: true,
			          fontFamily: 'monospace'
			        }
			      }
			    ],
			    // Disable init animation.
			    animationDuration: 0,
			    animationDurationUpdate: updateFrequency,
			    animationEasing: 'linear',
			    animationEasingUpdate: 'linear',
			    graphic: {
			      elements: [
			        { 
					  onclick: function () {
						 if (pending) {return;}
						 for (let i = startIndex-1; i < years.length - 1; ++i) {
						    pending = true;
						   (function (i) {
						     setTimeout(function () {
							   if(i>=years.length-2){pending = false};
						       updateYear(years[i+1]);
						     }, (i) * updateFrequency);
						   })(i);
						 }
						 function updateYear(year) {
						   let source = data.slice(1).filter(function (d) {
						     return d[2] === year;
						   });
						   option.series[0].data = source;
						   option.graphic.elements[0].style.text = year;
						   myChart.setOption(option);
						 }
					  },
			          type: 'text',
			          right: 160,
			          bottom: 60,
			          style: {
			            text: startYear,
			            font: 'bolder 80px monospace',
			            fill: 'rgba(100, 100, 100, 0.25)'
			          },
			          z: 100
			        }
			      ]
			    }
			  };
			  // console.log(option);
			  myChart.setOption(option);
			  window.addEventListener("resize", function() {
			              myChart.resize()
			            });

			})
		},
		drawTreeChart() {
			
			// domecharts  main
			var myChart = echarts.init(document.getElementById("radial-tree"));
			var option;
			
			myChart.showLoading();
			$.when(
			  $.getJSON('data/submit-location.json')
			).done(function (res0) {
			  const treeData = res0;
			  //console.log(treeData)
			  myChart.hideLoading();
			  myChart.setOption(
			    (option = {
			      tooltip: {
			        trigger: 'item',
			        triggerOn: 'mousemove',
					valueFormatter: (value) => value.toFixed(1)+'%'
			      },
			      series: [
			        {
			          type: 'tree',
			          data: [treeData],
			          top: '18%',
			          bottom: '14%',
			          layout: 'radial',
			          symbol: 'emptyCircle',
			          symbolSize: 7,
			          initialTreeDepth: 3,
			          animationDurationUpdate: 750,
			          emphasis: {
			            focus: 'descendant'
			          }
					  
			        }
			      ]
			    })
			  );
			  // console.log(option);
			  myChart.setOption(option);
			  window.addEventListener("resize", function() {
						  myChart.resize()
						})
			})
	    },
		drawScatterChart() {
		  // domecharts  main
		  var myChart = echarts.init(document.getElementById("bubble-scatter"));
		  const scatterData = [
		      [[1338, 15201, 2287, '', ''], [115, 9711, 1822, '', ''], [1122, 6566, 1748, '', ''], [1122, 6566, 1748, '', ''], [1431, 5528, 1867, '', ''], [1324, 5529, 1890, '', ''], [1382, 5620, 1461, '', ''], [59, 6854, 1951, '50', ''], [1180, 5783, 1501, '', ''], [61, 7139, 1595, 'ETC', '']]
		    
		  ];
		  
		  
		  // 
		  var option = {
			  xAxis: {
			    name: '',
				max:2000,
				axisLabel: {
				  formatter: function (n) {
				    return n;
				  }
				},
				splitLine: { show: false }
			  },
			  yAxis: {
				  name: '',
				  axisLabel: {
				    formatter: function (n) {
				      return n;
				    }
				  },
				splitLine: { show: false },
				scale: true
			  },
			  grid: {
				left: 40,
				right: 130
			  },
			  series: [
				{
				  name: '1990',
				  data: scatterData[0],
				  type: 'scatter',
				  symbolSize: function (data) {
					//console.log(data[2]);
					return Math.sqrt(data[2]);
				  },
				  itemStyle: {
				      normal: {
				        	color: function (data) {
								          
								          var dataRange = Math.floor((2400-data.data[2])/4).toString(16);
										  var dataColor = '#' + dataRange + dataRange+ 'ff';
										  //console.log(dataRange);
										  return dataColor;
									  }
				      }
