		drawMulitiRadar() {
		  
		  // domecharts  main
		  var myChart = echarts.init(document.getElementById("multi-radar"));
		  var option;
		  $.when(
		    $.getJSON('data/status-year.json')
		  ).done(function (res0) {
			  const dataRadar=res0;
			  //console.log("data:"+dataRadar);
			  // 
			   option = {
			     title: {
			       text: '',
			       subtext: '',
			       top: 10,
			       left: 10
			     },
			     tooltip: {
			       trigger: 'item'
			     },
			     legend: {
			       type: 'scroll',
			       bottom: 10,
			       data: ['2017','2018','2019','2020','2021']
			     },
			     visualMap: {
				   max:100,
				   min:75,
			       top: 'middle',
			       right: 10,
			       color: ['red', 'yellow'],
			       calculable: true
			     },
			     radar: {
			       indicator: [
			         { text: '', max: 10000 },
			         { text: '', max: 1000 },
			         { text: '', max: 7000 },
			         { text: '', max: 8000 },
			         { text: '', max: 100, min:50 }
			       ]
			     },
			     series: (function () {
			       var series = [];
			       for (var i = 1; i <= 5; i++) {
					 //console.log(dataRadar[i-1]);
			         series.push({
			           type: 'radar',
			           symbol: 'none',
			           lineStyle: {
			             width: 1
			           },
			           emphasis: {
			             areaStyle: {
			               color: 'rgba(0,250,0,0.3)'
			             }
			           },
			           data: [dataRadar[i-1]],
			         });
			       }
			       return series;
			     })()
			   };
			  // 
			  myChart.setOption(option);
			  window.addEventListener("resize", function() {
						  myChart.resize()
						})
			})
		},
